import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LTcare from '@/components/LTcare.vue'
import Caregiver from '@/components/Caregiver.vue'
import Commercial from '@/components/Commercial.vue'
//import CService from '@/components/CService.vue'
import CRegister from '@/components/CRegister.vue'
import CareInfo from '@/components/CareInfo.vue'
import CareVideo from '@/components/CareVideo.vue'
import CommentStar from '@/components/CommentStar.vue'
import TLesson from '@/components/TLesson.vue'
import ContiL from '@/components/ContiL.vue'
import LCheck from '@/components/LCheck.vue'
//import MentalCare from '@/components/MentalCare.vue'
import FeeStructure from '@/components/FeeStructure.vue'
import PartnersR from '@/components/PartnersR.vue'
import MarketIng from '@/components/MarketIng.vue'
import WVolunteer from '@/components/WVolunteer.vue'
import HourCal from '@/components/HourCal.vue'

const routes = [
  { 
    //根目錄被設為HomeView
    path: '/',
    name: 'home',
    component: HomeView
  },
  { path: '/LTcare', component: LTcare },
  { path: '/Caregiver', component: Caregiver },
  { path: '/Commercial', component: Commercial },
  //{ path: '/CService', component: CService },
  //按鈕直接撥號，網頁好像可以以手機方式撥號
  { path: '/LTcare/CRegister', component: CRegister },
  { path: '/LTcare/CareInfo', component: CareInfo },
  { path: '/LTcare/CareVideo', component: CareVideo },
  { path: '/LTcare/CommentStar', component: CommentStar },
  { path: '/Caregiver/TLesson', component: TLesson },
  { path: '/Caregiver/ContiL', component: ContiL },
  { path: '/Caregiver/LCheck', component: LCheck },
  //{ path: '/Caregiver/MentalCare', component: MentalCare }
  { path: '/Commercial/FeeStructure', component: FeeStructure },
  { path: '/Commercial/PartnersR', component: PartnersR },
  { path: '/Commercial/MarketIng', component: MarketIng },
  { path: '/Commercial/WVolunteer', component: WVolunteer },
  { path: '/Commercial/HourCal', component: HourCal }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
