<template>
    <div>
        <h1>步驟1 >> 身份證正反面上傳</h1>
        <div class="upload-conatiner">
            <div class="image-upload">
                <label for="front" class="image-label">
                    <img v-if="frontImage" :src="frontImage" alt="身份證正面" style="max-width: 200px; margin-top: 10px;" />
                    <img v-else src="@/assets/images/idF.png" alt="上傳身份證正面" class="upload-image" />
                    <input type="file" id="front" @change="handleFileChange('front', $event)" accept="image/*" />
                </label>
                <span>上傳身份證正面</span>
            </div>
            <div class="image-upload">
                <label for="back" class="image-label">
                    <img v-if="backImage" :src="backImage" alt="身份證反面" class="upload-image" />
                    <img v-else src="@/assets/images/idB.png" alt="上傳身份證反面" class="upload-image" /><input type="file"
                        id="back" @change="handleFileChange('back', $event)" accept="image/*" />
                    <input type="file" id="back" @change="handleFileChange('back', $event)" accept="image/*" />
                </label>
                <span>上傳身份證反面</span>
            </div>
        </div>
        <button @click="submit">提交</button>

        <div class="divider"></div> <!-- 用于分隔步骤1和步骤2 -->

        <!-- 证照上传部分 -->
        <div v-if="isIDUploaded" class="upload-container">
            <h2>步驟2 >> 證照上傳</h2>
            <div class="image-upload">
                <label for="license" class="image-label">
                    <input type="file" id="license" @change="handleLicenseChange" accept=".pdf" />
                    <span class="upload-label">上傳證照(PDF格式)</span>
                </label>
                <button @click="submitLicense" v-if="licenseFile">提交證照</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'LCheck',
    data() {
        return {
            frontImage: null,
            backImage: null,
            frontFile: null,
            backFile: null,
            isIDUploaded: false,    //用於標記身分證是否上傳成功
            licenseFile: null,
        };
    },
    methods: {
        handleFileChange(side, event) {
            const file = event.target.files[0];
            if (file) {
                const reader = new FileReader();
                reader.onload = (e) => {
                    if (side === 'front') {
                        this.frontImage = e.target.result;
                        this.frontFile = file;
                    } else {
                        this.backImage = e.target.result;
                        this.backFile = file;
                    }
                };
                reader.readAsDataURL(file);
            }
        },
        submit() {
            if (!this.frontFile || !this.backFile) {
                alert('請上傳身份證的正反面');
                return;
            }

            // 此处可添加身份证上传的逻辑，暂时省略
            this.isIDUploaded = true; // 设置标记为上传身份证成功
            alert('身份證上傳成功，接下来上傳證照。');
        },
        handleLicenseChange(event) {
            this.licenseFile = event.target.files[0];
        },
        submitLicense() {
            if (!this.licenseFile) {
                alert('请上傳證照文件');
                return;
            }

            // 此处可添加证照上传的逻辑，暂时省略
            alert('證照上傳成功！');


            // 发送文件到服务器（根据实际情况更改URL）
            //         fetch('https://your-server-url.com/upload', {
            //             method: 'POST',
            //             body: formData,
            //         })
            //             .then(response => response.json())
            //             .then(data => {
            //                 console.log('上傳成功:', data);
            //                 alert('上傳成功');
            //             })
            //             .catch(error => {
            //                 console.error('上傳失敗:', error);
            //                 alert('上傳失敗');
            //             });
        },
    },
}
</script>

<style scoped>
h1,
h2 {
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: bolder;
    text-align: center;
    color: #2c3e50;
}

.accordion-button {
    font-size: 18px;
}

.accordion-body ul {
    list-style: none;
    padding: 0;
    text-align: left;
}

.accordion-body ul li {
    margin: 5px 0;
}

.accordion-body p {
    text-align: left;
}

button {
    margin-top: 10px;
}

.image-upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.image-label {
    cursor: pointer;
}

.upload-image {
    max-width: 200px;
    height: auto;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-bottom: 10px;
}

input[type="file"] {
    display: none;
    /* 隐藏实际的文件输入 */
}

.divider {
    border-top: 1px solid #ccc;
    /* 中间的分隔线 */
    margin: 20px 0;
}
</style>
