import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css'; // Element Plus 的樣式
import zhTw from 'element-plus/es/locale/lang/zh-tw'; // 使用繁體中文
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import { library } from '@fortawesome/fontawesome-svg-core'
import { faHome, faUser, faBriefcase, faWrench } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faHome, faUser, faBriefcase, faWrench)

const app = createApp(App)

app.component('font-awesome-icon',FontAwesomeIcon)
app.use(store)
app.use(router)
app.use(ElementPlus, { locale: zhTw }); // 設置繁體中文語言

app.mount('#app')
