<template>
    <div>
        <h1>照護登記頁面</h1>
        <div class="service-form">
            <el-form ref="form" :model="formData" label-width="120px">
                <!-- 服務類別 -->
                <el-form-item label="服務類別">
                    <el-select v-model="formData.serviceType" multiple placeholder="選擇服務類別">
                        <el-option v-for="type in serviceTypes" :key="type" :label="type" :value="type" />
                    </el-select>
                </el-form-item>

                <!-- 服務日期 -->
                <el-form-item label="服務日期">
                    <el-date-picker v-model="formData.serviceDate" type="daterange" range-separator="至"
                        start-placeholder="開始日期" end-placeholder="結束日期" />
                </el-form-item>

                <!-- 開始時間與結束時間 -->
                <el-form-item label="開始時間">
                    <el-time-picker v-model="formData.startTime" placeholder="選擇開始時間" />
                </el-form-item>
                <el-form-item label="結束時間">
                    <el-time-picker v-model="formData.endTime" placeholder="選擇結束時間" />
                </el-form-item>

                <!-- 工作項目 -->
                <el-form-item label="工作項目">
                    <el-input v-model="formData.task" placeholder="輸入工作項目" />
                </el-form-item>

                <!-- 服務地點 -->
                <el-form-item label="服務對象">
                    <el-input v-model="formData.client.name" placeholder="點擊設置服務對象資料" @focus="openDialog" readonly />
                </el-form-item>

                <!-- 提交按鈕 -->
                <el-form-item>
                    <el-button type="primary" @click="submitForm">提交</el-button>
                    <el-button @click="resetForm">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!-- 彈窗 -->
        <el-dialog v-model="dialogVisible" title="設置服務對象資料" width="50%">
            <el-form :model="clientData" label-width="120px">
                <!-- 姓 -->
                <el-form-item label="姓">
                    <el-input v-model="clientData.lastName" />
                </el-form-item>
                <!-- 名 -->
                <el-form-item label="名">
                    <el-input v-model="clientData.firstName" />
                </el-form-item>
                <!-- 生日 -->
                <el-form-item label="生日">
                    <el-date-picker v-model="clientData.birthday" type="date" placeholder="選擇生日" />
                </el-form-item>
                <!-- 生理性別 -->
                <el-form-item label="生理性別">
                    <el-radio-group v-model="clientData.gender">
                        <el-radio label="男">男</el-radio>
                        <el-radio label="女">女</el-radio>
                    </el-radio-group>
                </el-form-item>
                <!-- 身高 -->
                <el-form-item label="身高">
                    <el-input-number v-model="clientData.height" :min="0" :max="300" />
                </el-form-item>
                <!-- 體重 -->
                <el-form-item label="體重">
                    <el-input-number v-model="clientData.weight" :min="0" :max="300" />
                </el-form-item>
                <!-- 身分證字號 -->
                <el-form-item label="身分證字號">
                    <el-input v-model="clientData.idNumber" />
                </el-form-item>
                <!-- 與填寫關係 -->
                <el-form-item label="與填寫人關係">
                    <el-input v-model="clientData.relationship" />
                </el-form-item>
                <!-- 情況描述 -->
                <el-form-item label="情況描述">
                    <el-input type="textarea" v-model="clientData.description" />
                </el-form-item>
                <!-- 服務地點 -->
                <el-form-item label="縣市">
                    <el-select v-model="clientData.location.city" placeholder="選擇縣市">
                        <el-option label="台北市" value="台北市" />
                        <el-option label="高雄市" value="高雄市" />
                    </el-select>
                </el-form-item>
                <el-form-item label="地址">
                    <el-input v-model="clientData.location.address" />
                </el-form-item>
                <!-- 緊急聯絡人 -->
                <el-form-item label="緊急聯絡人">
                    <el-input v-model="clientData.emergencyContact.lastName" />
                </el-form-item>
                
                <el-form-item label="緊急聯絡手機">
                    <el-input v-model="clientData.emergencyContact.phone" />
                </el-form-item>
            </el-form>

            <!-- 彈窗底部操作按鈕 -->
            <template #footer>
                <el-button @click="dialogVisible = false">取消</el-button>
                <el-button type="primary" @click="saveClientData">保存</el-button>
            </template>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'CRegister',
    data() {
        return {
            // 預設資料
            formData: {
                serviceType: [], // 服務類別
                serviceDate: null, // 日期範圍
                startTime: '', // 開始時間
                endTime: '', // 結束時間
                task: '', // 工作項目
                location: '', // 服務地點
                client: {
                    name: '', // 對象姓名
                    age: null, // 對象年齡
                    contact: '', // 聯絡方式
                },
            },
            clientData: {
                lastName: '',
                firstName: '',
                birthday: null,
                gender: '',
                height: null,
                weight: null,
                idNumber: '',
                relationship: '',
                description: '',
                location: { city: '', address: '' },
                emergencyContact: { lastName: '', firstName: '', phone: '' },
            },
            dialogVisible: false, // 初始化彈窗狀態
            serviceTypes: [
                "物理治療師",
                "職能治療師",
                "護理師/護士",
                "照顧服務員",
                "藝術府療師",
                "營養師",
            ],
        };
    },
    methods: {
        // 提交表單
        openDialog() {
            this.dialogVisible = true;
        },
        saveClientData() {
            if (!this.clientData.lastName || !this.clientData.firstName) {
                alert("請填寫完整的姓名！");
                return;
            }
            this.formData.client.name = `${this.clientData.lastName} ${this.clientData.firstName}`;
            this.dialogVisible = false; // 關閉彈窗
            alert("服務對象資料已保存！");
        },

        submitForm() {
            console.log("表單資料:", this.formData);
            alert("表單已提交！");
        },
        // 重置表單
        resetForm() {
            if (this.$refs.form) {
                this.$refs.form.resetFields();
            } else {
                console.error("表單引用無法找到，請檢查 ref 設置是否正確。");
            }
            this.$refs.form?.resetFields();
            // 確保資料結構完全重置
            this.formData = {
                serviceType: [],
                serviceDate: null,
                startTime: '',
                endTime: '',
                task: '',
                location: '',
                client: {
                    name: '',
                    age: null,
                    contact: '',
                },
            };
        },
    },
};
</script>

<style scoped>
h1 {
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: bolder;
    text-align: center;
    color: #2c3e50;
}

.service-form {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fff;
}
label{
    font-size: 80px;
}
</style>
