<template>
    <div class="review-system">
        <h1>用戶評論</h1>

        <!-- 評分與評論 -->
        <div class="review-input">
            <div class="rating">
                <button v-for="star in 5" :key="star" :class="{ active: star <= userRating }" @click="setRating(star)">
                    ★
                </button>
            </div>
            <textarea v-model="userComment" placeholder="輸入您的評論（可選）" class="comment-box"></textarea>
            <button @click="submitReview" class="submit-btn">提交評論</button>
        </div>

        <!-- 排序選擇 -->
        <div class="sort-options">
            <label>排序：</label>
            <select v-model="sortOrder" @change="sortReviews">
                <option value="latest">最新評論</option>
                <option value="highest">高分到低分</option>
                <option value="lowest">低分到高分</option>
            </select>
        </div>

        <!-- 顯示評論 -->
        <div class="review-list">
            <div v-for="review in sortedReviews" :key="review.id" class="review-item">
                <div class="review-header">
                    <div class="review-rating">
                        <template v-if="review.rating">
                            <span v-for="star in review.rating" :key="star">★</span>
                        </template>
                        <span v-else class="no-rating">未評分</span>
                    </div>
                    <span class="review-date">{{ review.date }}</span>
                </div>
                <p class="review-comment">{{ review.comment }}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            userRating: 0, // 用戶選擇的星級
            userComment: "", // 用戶輸入的評論內容
            reviews: [], // 存儲所有評論
            sortOrder: "latest", // 排序選擇：latest, highest, lowest
        };
    },
    mounted() {
        const storedReviews = JSON.parse(localStorage.getItem('reviews')) || [];
        this.reviews = storedReviews;
    },
    computed: {
        // 排序後的評論
        sortedReviews() {
            if (this.sortOrder === "highest") {
                return [...this.reviews].sort((a, b) => b.rating - a.rating);
            } else if (this.sortOrder === "lowest") {
                return [...this.reviews].sort((a, b) => a.rating - b.rating);
            } else {
                return [...this.reviews].sort((a, b) => new Date(b.date) - new Date(a.date));
            }
        },
    },
    methods: {
        // 設置用戶選擇的星級
        setRating(star) {
            this.userRating = star;
        },
        // 提交評論
        submitReview() {
            if (this.userRating === 0 && !this.userComment.trim()) {
                alert("請輸入評論內容或給予星級評分！");
                return;
            }

            const newReview = {
                id: Date.now(), // 獨特 ID
                rating: this.userRating || null, // 如果未評分，設置為 null
                comment: this.userComment.trim(),
                date: new Date().toLocaleString(),
            };

            // 將新評論儲存在本地存儲
            const storedReviews = JSON.parse(localStorage.getItem('reviews')) || [];
            storedReviews.push(newReview);
            localStorage.setItem('reviews', JSON.stringify(storedReviews));

            console.log('新評論:', newReview);
            console.log('已存儲的評論:', storedReviews);

            // 更新本地評論數據
            this.reviews = storedReviews;

            // 重置表單
            this.userRating = 0; // 重置評分
            this.userComment = ""; // 清空評論框
        },
        // 排序評論
        sortReviews() {
            // 透過 computed 自動更新
        },
    },
};
</script>

<style scoped>
.no-rating {
    color: #aaa;
    font-style: italic;
}

.review-system {
    padding: 20px;
}

.review-input {
    margin-bottom: 20px;
}

.rating button {
    font-size: 24px;
    background: none;
    border: none;
    cursor: pointer;
    color: gray;
}

.rating button.active {
    color: gold;
}

.comment-box {
    display: block;
    width: 100%;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.submit-btn {
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.sort-options {
    margin-bottom: 20px;
}

.review-list {
    margin-top: 20px;
}

.review-item {
    border: 1px solid #ddd;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 4px;
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.review-rating {
    color: gold;
}
</style>
